.modal-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background: url('../modal-back.jpg');
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    text-align: center;
  }
  
  .modal-close-button {
    margin-top: 10px;
    padding: 8px 16px;
    background: #ccc;
    color: #1d151c;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  